    <div style="width:80%">
        <a-form :form="form"  @submit="handleSubmit">
            <a-row :gutter="24">
                <a-col :span="8">
                    <a-form-item
                    :labelCol="labelCol"
                    :wrapperCol="wrapperCol"
                    label="姓名">
                    <a-input
                        v-decorator="['note', { rules: [{ required: true, message: 'Please input your name!' }] }]"
                    />
                    </a-form-item>
                </a-col>
                <a-col :span="8">
                    <a-form-item
                    :labelCol="labelCol"
                    :wrapperCol="wrapperCol"
                    label="年龄">
                    <a-input
                        v-decorator="['age', { rules: [{ required: true, message: 'Please input your age!' }] }]"
                    />
                    </a-form-item>
                </a-col>
                <a-col :span="8">
                    <a-form-item
                    :labelCol="labelCol"
                    :wrapperCol="wrapperCol"
                    label="性别">
                    <a-select
                        v-decorator="[
